let mainPage = document.querySelector(".index-page")
gsap.registerPlugin(ScrollTrigger);
gsap.registerPlugin(TweenMax);


if(mainPage){

    serviceCardsAnim()
    planetAnim()
    equipAnim()
}

function planetAnim(){
    let scaleItems = document.querySelectorAll(".scale__item")

    scaleItems.forEach(scaleItem => {
        gsap.to(scaleItem, {
            scrollTrigger: {
                trigger: scaleItem,
                start: 'top 35%',
                end: 'bottom 30%',
                scrub: true,
                toggleClass: {targets: scaleItem, className: "_show"},
            }
        })
    })



    let planet = document.querySelector(".scale__planet-img")
    let star = document.querySelector(".scale__planet-star")

    const tl = gsap.timeline({
        scrollTrigger: {
            trigger: planet,
            start: 'top 35%',
            end: 'bottom 30%',
            scrub: true,
        }
    })


    if(window.innerWidth<577){
        tl.to(star, {
            xPercent: 25,
            yPercent: 25,
        }).to(star, {
            xPercent: 45,
            yPercent: 45,
        }).to(star, {
            xPercent: 55,
            yPercent: 65,
        }).to(star, {
            xPercent: 65,
            yPercent: 75,
        }).to(star, {
            xPercent: 70,
            yPercent: 85,
        }).to(star, {
            xPercent: 75,
            yPercent: 95,
        }).to(star, {
            xPercent: 80,
            yPercent: 105,
        }).to(star, {
            xPercent: 85,
            yPercent: 115,
        }).to(star, {
            xPercent: 90,
            yPercent: 135,
        }).to(star, {
            xPercent: 95,
            yPercent: 155,
        }).to(star, {
            xPercent: 100,
            yPercent: 175,
        }).to(star, {
            xPercent: 103,
            yPercent: 225,
        }).to(star, {
            xPercent: 100,
            yPercent: 255,
        }).to(star, {
            xPercent: 95,
            yPercent: 285,
        }).to(star, {
            xPercent: 85,
            yPercent: 315,
        })


        return
    }

    tl.to(star, {
        xPercent: 45,
        yPercent: 25,
    }).to(star, {
        xPercent: 95,
        yPercent: 65,
    }).to(star, {
        xPercent: 120,
        yPercent: 95,
    }).to(star, {
        xPercent: 140,
        yPercent: 125,
    }).to(star, {
        xPercent: 150,
        yPercent: 155,
    }).to(star, {
        xPercent: 160,
        yPercent: 185,
    }).to(star, {
        xPercent: 165,
        yPercent: 205,
    }).to(star, {
        xPercent: 165,
        yPercent: 225,
    }).to(star, {
        xPercent: 165,
        yPercent: 255,
    }).to(star, {
        xPercent: 165,
        yPercent: 275,
    })
}


function equipAnim(){
    let elementsFadeOut = document.querySelectorAll(".set-equip__group .h4,.set-equip__group li")

    elementsFadeOut.forEach(el => {
        gsap.to(el, {
            opacity: 0,
            scrollTrigger: {
                trigger: el,
                start: 'top 30% top',
                end: 'bottom -5%',
                scrub: true,
            }
        })
    })
}


function serviceCardsAnim() {
    if(window.innerWidth<993){
        return
    }

    let cards = gsap.utils.toArray('.service-card');

    gsap.to(cards, {
        xPercent: -100 * (cards.length - 2.2),
        delay: 0.2,
        scrollTrigger: {
            trigger: '.your-way__items',
            start: 'top 25% top',
            end: `+=${cards.length*1000}`,
            // snap: 1 / (sections.length - 1),
            pin: true,
            scrub: true,
            // markers: true
        }

    })
}
