window.addEventListener('DOMContentLoaded', () => {
    fix100vh();
    window.addEventListener('resize', () => {
        fix100vh();
        getScrollBarSize()
    })

    getScrollBarSize()

    let loader = document.querySelector(".loader")
    let video = document.querySelector('#video');
    let mainTitle = document.querySelector('.main .h1');
    let mainText = document.querySelector('.main__text');
    if(loader){
        video.addEventListener('click',function(){
            video.play();
        },false);
        window.scrollTo(0,0);
        body.classList.add("_loading")
        setTimeout(()=>{
            video.play();
        },2000)

        setTimeout(()=>{
            loader.classList.remove("_show")

            setTimeout(()=>{
                mainTitle.classList.add("_show")
            },8000)

            setTimeout(()=>{
                mainText.classList.add("_show")
                body.classList.remove("_loading")
            },9000)
        },5000)
    }
})

function fix100vh() {
    let vh = window.innerHeight * 0.01;
    document.documentElement.style.setProperty('--vh', `${vh}px`);
}
